<script>
import { useRouter } from "vue-router";
import { ref } from "vue";

export default {
  name: "TermsOfService",

  setup() {
    //  checkbox  
    const agree01 = ref(false);
    const agree02 = ref(false);
    const agree03 = ref(false);
    const agreeAll = ref(false);

    const selectAll = () => {
      const allSelected = agree01.value && agree02.value && agree03.value;
      agreeAll.value = !allSelected;
      agree01.value = !allSelected;
      agree02.value = !allSelected;
      agree03.value = !allSelected;
    };

    //  checkbox  ->     -> account checkbox 
    const check = () => {
      const selectedTerms = {
        agree01: agree01.value,
        agree02: agree02.value,
        agree03: agree03.value,
        agreeAll: agreeAll.value,
      };
      //     
      console.log("selected", selectedTerms);
      router.push({ name: "SignUp", query: selectedTerms });
    };

    const router = useRouter();

    const cancel = () => {
      router.push({ name: "SignUp" });
    };

    return { agree01, agree02, agree03, agreeAll, selectAll, check, cancel };
  },
};
</script>

