        <div class="benefit01-bubble">
          <img class="circle" src="../assets/images/  .png" alt=" " />
          <p class="benefit01-text"> 01</p>
          <p class="benefit01-title">   </p>
          <p class="benefit01-content">
                  <br />
                  
          </p>
        </div>
        <div class="benefit02-bubble">
          <img class="circle" src="../assets/images/  .png" alt=" " />
          <p class="benefit02-text"> 02</p>
          <p class="benefit02-title">   &  </p>
          <p class="benefit02-content">
                 2<br />
               10%  
          </p>
        </div>
        <div class="benefit03-bubble">
          <img class="circle" src="../assets/images/  .png" alt=" " />
          <p class="benefit03-text"> 03</p>
          <p class="benefit03-title">PT  & 1:1   </p>
          <p class="benefit03-content">
               1:1 PT   4<br />
               1:1  
          </p>
        </div>
      </div>
    </div>
    <div class="content-bottom" id="target">
      <h2 class="bottom-title">
          
        <!-- <p class="bottom-subtitle">membership benefits</p> -->
      </h2>
      <div v-for="(item, index) in membership" :key="index" :class="`benefit0${index + 1}-box`">
        <img class="emoji" :src="item.icon" :alt="`${item.grade} `" />
        <p class="benefit-price">{{ item.price.toLocaleString() }}/</p>
        <p class="benefit-rank">LV. {{ item.grade }}</p>
        <p class="benefit-full-text" v-html="item.description"></p>
        <button @click="goToPayment(item.id)" class="btn">
          
        </button>
      </div>
    </div>
  </main>
</template>

<script>
import { ref, computed } from "vue";
import { useRouter } from "vue-router";
import { useStore } from "vuex";
import apiClient from "@/api/apiClient";

export default {
  name: "Membership",

  setup() {
    const router = useRouter();
    const store = useStore();

    //    
    const membership = ref([
      {
        id: 1,
        grade: "",
        price: 5000,
        icon: require("@/assets/images/baby.png"),
        description: " 01 <br />    <br />   !"
      },
      {
        id: 2,
        grade: "",
        price: 10000,
        icon: require("@/assets/images/run.png"),
        description: " 01 &  02 <br />    <br />     !"
      },
      {
        id: 3,
        grade: "+",
        price: 20000,
        icon: require("@/assets/images/arm.png"),
        description: " 01 & 02 & 03 <br />I-Fit    <br />   !"
      },
    ]);

    const goToPayment = async (id) => {
      const isLoggedIn = computed(() => store.getters['isLogged/loggedIn']);

      if (!isLoggedIn.value) {
        alert("   .");
        router.push({ name: "SignIn" });
        return;
      }

      try {
        const response = await apiClient.get('/payment/user-info', {
          params: {
            id
          }
        });

        const paymentData = response.data;

        router.push({
          name: "Payment",
          query: {
            username: paymentData.username,
            membershipInfo: JSON.stringify(paymentData.membershipInfo),
            points: paymentData.points,
            coupons: JSON.stringify(paymentData.coupons)
          }
        });
      } catch (error) {
        console.log("  error", error);
      }
    }

    //   
    // const goToPayment = (membershipGrade, membershipPrice) => {
    //   console.log('  :', membershipGrade, membershipPrice);

    //   router.push({
    //     name: "Payment",
    //     query: { 
    //       membershipGrade,
    //       membershipPrice
    //     }
    //   });
    // };


    return {
      membership,
      goToPayment,
    };
  },
};
</script>


