<template>
  <main>
    <form @submit.prevent="handleSubmit" method="post" class="signup">
      <div class="signup-container">
        <h1></h1>
        <p>     !</p>
        <div class="signup-id input-block">
          <label class="signup-label" for="id">
            <span class="available" v-if="idAvailable === true">  .</span>
            <span class="notavailable" v-else-if="idAvailable === false">
                 .
            </span>
          </label>
          <div class="signup-field">
            <input type="text" id="id" name="id" placeholder=" ." class="signup-input"
              v-model="formData.loginId" />
            <button type="button" class="signup-btn" @click="checkId">
               
            </button>
          </div>
        </div>
        <div class="signup-pw input-block">
          <label class="signup-label" for="password"></label>
          <div class="signup-field">
            <input type="password" id="password" name="password" placeholder=" ." class="signup-input"
              v-model="formData.password" />
          </div>
        </div>
        <div class="signup-pw-check input-block">
          <label class="signup-label" for="password-check"> 
            <!--   -->
            <span v-if="Bothpasswords && passwordsMatch" class="password-match"></span>
            <!--   -->
            <span v-else-if="Bothpasswords && !passwordsMatch" class="password-mismatch"> </span>
          </label>
          <div class="signup-field">
            <input type="password" id="password-check" name="passwordcheck" placeholder=" " class="signup-input"
              v-model="passwordCheck" />
          </div>
        </div>
        <div class="signup-name input-block">
          <label class="signup-label" for="name"></label>
          <div class="signup-field">
            <input type="text" id="name" name="name" placeholder=" ." class="signup-input"
              v-model="formData.username" />
          </div>
        </div>
        <div class="signup-phone input-block">
          <label class="signup-label" for="phone"></label>
          <div class="signup-field">
            <input type="text" id="phone" name="phone" placeholder=" ." class="signup-input"
              v-model="formData.phoneNumber" />
          </div>
        </div>

        <div class="signup-email input-block">
          <label class="signup-label" for="email"></label>
          <div class="signup-field">
            <input type="text" id="email" name="email" placeholder=" ." class="signup-input"
            v-model="emailValue" />
            <button type="button" class="signup-btn" @click="sendEmailRequest" :disabled="timerStarted">
               
            </button>
            <div v-if="loading" class="spinner"></div>
          </div>
        </div>

        <div class="signup-email-auth input-block">
          <label class="signup-label" for="email-number"> 
            <span v-if="timerStarted && timeLeft > 0" class="timer">{{ minutes }}:{{ seconds }}</span>
            <button type="button" v-if="!timerStarted && timeLeft === 0" class="re-request-btn"
              @click="handleReRequest">
              
            </button>
          </label>
          <div class="signup-field">
            <input type="text" id="email-number" name="email-number" placeholder=" ." class="signup-input"
              v-model="enteredCode" />
            <button type="button" class="signup-btn" @click="updateEmailAfterCheck">
              
            </button>
          </div>
        </div>
        <div class="signup-terms">
          <label class="signup-terms-label">
            <input type="checkbox" name="terms" value="agree" v-model="agreeAll" @click="selectAll" />
            <span class="signup-terms-text">  <span @click="goTos">&#62;</span></span>
          </label>
          <label class="signup-terms-label">
            <input type="checkbox" name="terms1" value="agree-1" id="agree01" v-model="agree01" />
            <span class="signup-terms-text"> ()<span @click="goTos">&#62;</span></span>
          </label>
          <label class="signup-terms-label">
            <input type="checkbox" name="terms2" value="agree-2" id="agree02" v-model="agree02" />
            <span class="signup-terms-text">    ()<span @click="goTos">&#62;</span></span>
          </label>
          <label class="signup-terms-label">
            <input type="checkbox" name="terms3" value="agree-3" id="agree03" v-model="agree03" />
            <span class="signup-terms-text">   ()<span @click="goTos">&#62;</span></span>
          </label>
        </div>
        <button class="signup-terms-btn" type="button" @click="account">
          
        </button>
        <p class="signup-account-check">
            ?<span class="signup-account-check-text" @click="goSignIn"> </span>
        </p>
      </div>
    </form>
  </main>
</template>

<script>
import { ref, computed, onMounted, watch } from "vue";
import { useRouter, useRoute } from "vue-router";
import { useEmail } from "@/services/sendEmail";
import axios from "axios";
import { toRaw } from "vue";

export default {
  name: "SignUp",

  setup() {
    const router = useRouter();

    const formData = ref({
      loginId: "",
      password: "",
      username: "",
      phoneNumber: "",
      email: "",
    });
    const passwordCheck = ref("");
    const enteredCode = ref("");
    const emailValue = ref("");

    onMounted(() => {
      const storedData = JSON.parse(localStorage.getItem('signupData')) || {};
      formData.value = { ...formData.value, ...storedData };
      passwordCheck.value = storedData.passwordCheck || "";
      emailValue.value = storedData.emailValue || "";
      enteredCode.value = storedData.enteredCode || "";

      window.addEventListener('beforeunload', () => {
        localStorage.removeItem('signupData');
      });
    });

    watch([formData, passwordCheck, enteredCode], ([newFormData, newPasswordCheck, newEmailValue, newEnteredCode]) => {
      localStorage.setItem('signupData', JSON.stringify({
        ...newFormData,
        passwordCheck: newPasswordCheck,
        emailValue: newEmailValue,
        enteredCode: newEnteredCode,
      }));
    }, { deep: true });

    watch(router.currentRoute, (to) => {
      if (to.path === '/sign-up') {
        const FromTerms = router.currentRoute.value.path === '/terms-of-service';
        if (!FromTerms) {
          localStorage.removeItem('signupData');
          formData.value = {
            loginId: "",
            password: "",
            username: "",
            phoneNumber: "",
            email: "",
          };
          passwordCheck.value = "";
          emailValue.value = "";
          enteredCode.value = "";
        }
      }
    });

    // formData    
    console.log(toRaw(formData.value));
    //      

    const passwordsMatch = computed(() => {
      return formData.value.password === passwordCheck.value;
    })
    //      
    const Bothpasswords = computed(
      () => formData.value.password && passwordCheck.value
    );

    const idAvailable = ref(null);
    //    
    const checkId = async () => {
      try {
        const response = await axios.post("/api/check-id", {
          loginId: formData.value.loginId,
        });

        if (response.data === " .") {
          idAvailable.value = true;
          alert("  .");
        } else if (response.data === " .") {
          idAvailable.value = false;
          alert("  .");
        } else {
          idAvailable.value = false;
          alert("");
        }
      } catch (error) {
        console.error(error);
        alert("     .");
      }
    };

    //   
    const sendEmailRequest = async () => {
      try {
        await sendEmail(emailValue.value);
      } catch (error) {
        alert("  .");
      }
    };
    //   
    const updateEmailAfterCheck = async () => {
      try {
        const isVerified = await verifyEmail(emailValue.value, enteredCode.value);
        console.log(" : ", isVerified);
        if (isVerified) {
          formData.value.email = emailValue.value; //    email formData 
          alert(" ");
        } else {
          alert("  ,  .");
        }
      } catch (error) {
        console.error("  : ", error);
      }
    };

    console.log(toRaw(formData));

    //      (formData)  
    const account = async () => {

      if (!passwordsMatch.value) {
        alert("  .");
        return;
      }
      if (!idAvailable.value) {
        alert("   .");
        return;
      }
      if (formData.value.username === null) {
        alert(" .");
        return;
      }
      if (formData.value.phoneNumber === null) {
        alert(" .");
        return;
      }

      try {
        await axios.post("/api/user-account", formData.value);
        alert(" !");

        localStorage.removeItem('signupData');

        router.push({ name: "SignIn" });
      } catch (error) {
        console.log("    ");
      }
    };

    const goTos = () => {
      router.push({ name: "TermsOfService" });
    };

    const goSignIn = () => {
      router.push({ name: "SignIn" });
    };

    const selectAll = () => {
      const allSelected = agree01.value && agree02.value && agree03.value;
      agreeAll.value = !allSelected;
      agree01.value = !allSelected;
      agree02.value = !allSelected;
      agree03.value = !allSelected;
    };

    // tos     
    const route = useRoute();
    const query = route.query;

    const agree01 = ref(query.agree01 === "true");
    const agree02 = ref(query.agree02 === "true");
    const agree03 = ref(query.agree03 === "true");
    const agreeAll = ref(query.agreeAll === "true");

    const {
      sendEmail,
      verifyEmail,
      timeLeft,
      minutes,
      seconds,
      handleReRequest,
      timerStarted,
      loading,
    } = useEmail();

    return {
      goTos,
      goSignIn,
      account,

      agreeAll,
      agree01,
      agree02,
      agree03,
      selectAll,

      updateEmailAfterCheck,

      timeLeft,
      minutes,
      seconds,
      timerStarted,
      emailValue,
      enteredCode,
      sendEmail,
      verifyEmail,
      handleReRequest,
      sendEmailRequest,
      loading,

      formData,
      idAvailable,
      checkId,
      passwordCheck,
      passwordsMatch,
      Bothpasswords,
    };
  },
};
</script>

<style scoped>
.spinner {
  border: 4px solid black;
  border-radius: 50%;
  border-top: 4px solid #ccc;
  width: 24px;
  height: 24px;
  animation: spin 1s linear infinite;
  margin-top: 10px;
  margin-left: 10px;
}

/*  */
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.timer {
  color: red;
  font-size: 14px;
  margin-left: 170px;
}

.re-request-btn {
  margin-left: 170px;
  border: none;
  background-color: white;
  color: blue;
}

.password-match {
  margin-left: 225px;
  font-size: 14px;
  color: blue;
}

.password-mismatch {
  margin-left: 180px;
  font-size: 14px;
  color: red;
}

main {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 1400px;
}

.signup {
  margin-top: 50px;
}

.signup-container {
  width: 490px;
  /* height: 1190px; */
  height: 1250px;
  text-align: center;
  display: flex;
  flex-direction: column;
  border: 1px solid rgba(0, 0, 0, 0.164);
  /* margin-bottom: 100px; */
}

.signup-container h1 {
  margin: 50px 0px;
}

/* .signup-container p {
  margin-top: -20px;
  margin-bottom: 30px;
} */

.signup-desc {
  margin-top: -20px;
  margin-bottom: 30px;
}

.input-block {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
}

.signup-label {
  margin-bottom: 10px;
  font-weight: bold;
}

/*      */
.available {
  font-size: 14px;
  margin-left: 84px;
}

.notavailable {
  margin-left: 66px;
  font-size: 14px;
}

.signup-field {
  display: flex;
  width: 404px;
  height: 40x;
}

.signup-input {
  flex: 1;
  padding: 5px 0px 5px 10px;
  border: 1px solid rgba(0, 0, 0, 0.253);
  border-radius: 0.1875rem;
  box-sizing: border-box;
  width: 100px;
  height: 40px;
  outline: none;
}

.signup-input:hover {
  border: 1px solid black;
}

.signup-btn {
  padding: 8px 16px;
  margin-left: 10px;
  font-weight: bold;
  border: none;
  background-color: #007bff;
  color: white;
  border-radius: 0.1875rem;
  cursor: pointer;
}

.signup-id,
.signup-pw,
.signup-pw-check,
.signup-name,
.signup-phone,
.signup-email,
.signup-email-auth,
.signup-account-check {
  margin: 30px 0px 0px 40px;
}

input::placeholder {
  font-size: small;
}

.at {
  margin: 8px 0px 5px 10px;
}

.signup-email-domain {
  margin-left: 10px;
  padding: 8px;
  border: 1px solid rgba(0, 0, 0, 0.253);
  background-color: white !important;
  border-radius: 10px;
  font-size: 13.3333px;
  color: rgba(0, 0, 0, 0.616);
  outline: none;
}

.signup-terms {
  width: 380px;
  text-align: left;
  margin-top: 10px;
  margin: 30px 0px 0px 40px;
}

.signup-terms-label {
  display: block;
  margin-top: 20px;
  font-weight: bold;
  width: 400px;
}

.signup-terms-text span {
  font-size: 14px;
  font-weight: bold;
  float: right;
  cursor: pointer;
}

.signup-terms-text {
  margin-left: 7px;
  font-size: 13px;
}

.signup-terms-btn {
  width: 200px;
  height: 40px;
  border: none;
  border-radius: 0.1875rem;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  font-size: 20px;
  font-weight: bold;
  margin: 50px 0px -10px 143px;
}

.signup-account-check-text {
  color: #007bff;
  margin-left: 20px;
  margin-right: 45px;
  cursor: pointer;
}
</style>
