import { ref } from "vue";
import axios from "axios";
import { useTimer } from "@/utils/timer";
import apiClient from "@/api/apiClient";

export function useEmail() {
  const sendStatus = ref(null); //    
  const loading = ref(false);
  const { timeLeft, minutes, seconds, start, reset, timerStarted } =
    useTimer(300); //     

  const sendEmail = async (email) => {
    if (email == "") {
      alert(" .");
      return;
    }
    loading.value = true; //  
    try {
      const response = await axios.post("/api/sendVerificationCode", {
        email: email, // data     
      });

      //     
      alert(response.data); //     

      if (response.status === 200) {
        start(); //  
      } else {
        alert(" ");
      }
      sendStatus.value = response.status === 200 ? "success" : "failed";
    } catch (error) {
      sendStatus.value = "failed";
      alert("  ."); //   
    } finally {
      loading.value = false;
    }
  };

  const handleReRequest = async () => {
    reset();
    await sendEmail();
  };

  const verifyEmail = async (email, enteredCode) => {
    try {
      const response = await axios.post("/api/verifyEmail", {
        email: email,
        enteredCode: enteredCode,
      });
      if (response.status === 200) {
        return true;
      } else {
        alert(" : " + response.data.message);
        return false;
      }
    } catch (error) {
      alert("  .");
      return false;
    }
  };

  // ==============================================================
  //               
  const sendEmailToken = async (email) => {
    if (email == "") {
      alert(" .");
      return;
    }
    loading.value = true;
    try {
      const response = await apiClient.post(
        "/updateEmail/sendVerificationCode",
        {
          email: email, // data     
        }
      );

      //     
      alert(response.data); //     

      if (response.status === 200) {
        start(); //  
      } else {
        alert(" ");
      }
      sendStatus.value = response.status === 200 ? "success" : "failed";
    } catch (error) {
      sendStatus.value = "failed";
      alert("  ."); //   
    } finally {
      loading.value = false;
    }
  };

  //      
  const handleReRequestToken = async () => {
    reset();
    await sendEmailToken();
  };

  //         
  const verifyEmailToken = async (email, enteredCode) => {
    try {
      const response = await apiClient.post("/updateEmail/verifyEmail", {
        email: email,
        enteredCode: enteredCode,
      });
      if (response.status === 200) {
        return true;
      } else {
        alert(" : " + response.data.message);
        return false;
      }
    } catch (error) {
      alert("  .");
      return false;
    }
  };

  return {
    sendEmail,
    handleReRequest,
    verifyEmail,

    sendEmailToken,
    handleReRequestToken,
    verifyEmailToken,

    loading,
    timeLeft,
    minutes,
    seconds,
    timerStarted,
  };
}

export default useEmail;
