<template>
  <main>
    <!--   -->
    <div class="carousel">
      <div class="carousel-box" :style="{ transform: `translateX(-${currentIndex * 100}%)` }">
        <div class="carousel-item" v-for="item in items" :key="item.id">
          <img :src="item.images" :alt="item.content" class="slide-images" @click="carouselGroups(item.id)" />
          <div class="slide-content">
            {{ item.content }}
          </div>
        </div>
      </div>
      <div class="arrow" id="left" @click="prevSlide">&#9001;</div>
      <div class="arrow" id="right" @click="nextSlide">&#9002;</div>
    </div>

    <div class="main-bottom">
      <!-- , , ,  -->
      <div class="filter">
        <div class="filter-list">
          <select class="list-select" title=" " @change="SportGroupList">
            <option value="" selected disabled></option>
            <option v-for="sport in sports" :key="sport.key" :value="sport.sport">{{ sport.sport }}</option>
          </select>

          <div class="list-select" @click="openDaumApi">
            <input class="input-event" v-model="locationInput" type="text" placeholder="" />
          </div>

          <div>
            <VueDatePicker class="date-picker" placeholder="" locale="ko" v-model="date" :enable-time-picker="false"
              select-text="" cancel-text="" @date-update="dateClicked" format="yyyy-MM-dd" />
          </div>

          <div>
            <input type="time" class="time-picker" v-model="timeString" @change="timeSelected" placeholder="">
          </div>

          <form class="search-box">
            <input type="text" name="search" class="search-input" placeholder=" ." v-model="searchTerm"
              @keyup.enter="searchGroups" />
            <img src="../assets/images/search.icon.png" alt="search" class="search-icon" @click="searchGroups" />
          </form>
        </div>
        <div class="sort">
          <select class="sort-title" title="" v-model="selectedOption" @change="selectedGroupList">
            <option value="" selected disabled></option>
            <option v-for="option in options" :key="option.value" :value="option.value">
              {{ option.text }}
            </option>
          </select>
        </div>

        <!--    -->
        <div class="group">
          <div v-for="group in groups" :key="group.communityId" class="group-container">
            <div class="unicode-box">
              <img class="group-detail-icon" src="@/assets/images/info-icon.png" @click="openModal(group)" />

              <img
                :src="group.saved ? require('@/assets/images/saved-icon3.png') : require('@/assets/images/save-icon.png')"
                class="group-save-icon" @click="likeGroup(group.communityId)" />

              <img class="group-join-icon" src="@/assets/images/plus-icon2.png"
                @click="showConfirmPopup = true; currentGroupId = group.communityId" />

              <div v-if="showConfirmPopup && currentGroupId === group.communityId" class="confirm-popup">
                <div class="popup-content">
                  <p> ?</p>
                  <button class="confirm-btn" @click="confirmDeletion(group.communityId)">
                    
                  </button>
                  <button class="cancle-btn" @click="cancelDeletion(group)">
                    
                  </button>
                </div>
              </div>
            </div>
            <div class="sport-image-box">
              <img :src="getSportImage(group.sport)" alt=" " class="sport-image">
              <!-- <img src="../assets/images/yoga-image.png" alt=" " class="sport-image"> -->
            </div>
            <div class="group-information">
              <div class="user-info-and-group-sport">
                <p class="sport-text">{{ group.sport }}</p>
                <div class="user-info">
                  <span class="user-name">{{ group.username }}</span>
                  <img
                    :src="group.profileUrl ? `data:image/png;base64,${group.profileUrl}` : require('@/assets/images/default-profile.png')"
                    alt=" " class="user-img">
                </div>
              </div>
              <div class="group-text">
                <p class="new-title"> {{ group.title }}</p>
                <p class="date-and-time">{{ group.date }}{{ group.time }} <span class="location">| {{ group.location
                    }}</span>
                </p>
                <p class="person">: {{ group.peopleParticipation }} / {{ group.person }}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!--     -->
    <div class="modal" v-if="isModalOpen">
      <div class="modal-content">
        <span class="close" @click="closeModal">&times;</span>
        <p class="modal-description"><strong>  : </strong>{{ selectedItem ? selectedItem.content : "" }}</p>
        <p><strong>  : </strong>{{ selectedItem ? selectedItem.location : "" }}</p>
      </div>
    </div>

  </main>
</template>

<script>
import { ref, computed, onBeforeMount } from "vue";
import { useRouter } from "vue-router";
import axios from "axios";
import { useStore } from "vuex";
import apiClient from '@/api/apiClient';
// import { timePicker } from "vue3-timepicker";
// import { usePagination } from "@/utils/pagination";
// import InfiniteLoading from 'vue3-infinite-loading';


export default {
  name: "Home",
  components: {
    // timePicker,
    // Pagination,
    // InfiniteLoading,
  },

  data() {
    return {
      currentIndex: 0,
      items: [
        {
          //     
          id: 1,
          // content: "slide1",
          popularity: 2,
          createdAt: "2024-08-03",
          liked: false,
          images: require('@/assets/images/slide1.png')
        },
        {
          //  ,   
          id: 2,
          // content: "slide2",
          popularity: 5,
          createdAt: "2024-07-20",
          liked: false,
          images: require('@/assets/images/slide2.png')
        },
        {
          //   6 ~ 10  
          id: 3,
          // content: "slide3",
          popularity: 1,
          createdAt: "2024-08-01",
          liked: false,
          images: require('@/assets/images/slide3.png')
        },
        //      .
        // TODO -   
      ],
      slideInterval: null,
      sortOption: "",
      isModalOpen: false, //   
      selectedItem: null, //  
      locations: [],  //    
    };
  },
  mounted() {
    //       
    this.startAutoSlide();
  },
  beforeUnmount() {
    //     
    this.stopAutoSlide();
  },
  watch: {
    sortOption(newValue) {
      this.sortItems(newValue);
    },
  },
  methods: {
    startAutoSlide() {
      // 3  
      this.slideInterval = setInterval(() => {
        this.nextSlide();
      }, 4000);
    },
    stopAutoSlide() {
      //   ->    
      if (this.slideInterval) {
        clearInterval(this.slideInterval);
        this.slideInterval = null;
      }
    },
    prevSlide() {
      this.currentIndex =
        this.currentIndex > 0 ? this.currentIndex - 1 : this.items.length - 1;
    },
    nextSlide() {
      this.currentIndex =
        this.currentIndex < this.items.length - 1 ? this.currentIndex + 1 : 0;
    },
    sortItems(option) {
      switch (option) {
        case "1":
          // 
          this.items.sort((a, b) => b.popularity - a.popularity);
          break;
        case "2":
          //  ()
          this.items.sort(
            (a, b) => new Date(b.createdAt) - new Date(a.createdAt)
          );
          break;
        case "3":
          //  ()
          this.items.sort(
            (a, b) => new Date(a.createdAt) - new Date(b.createdAt)
          );
          break;
        default:
          break;
      }
    },
    openModal(group) {
      this.selectedItem = {
        content: group.topboxContent,
        location: group.fullLocation
      };
      this.isModalOpen = true;
    },
    closeModal() {
      this.isModalOpen = false;
      this.selectedItem = null;
    },
    //TODO -   
  },

  setup() {
    const selectedOption = ref('');
    const options = ref([
      // { value: '', text: '' },
      { value: '1', text: '' },
      { value: '2', text: '' },
      { value: '3', text: '' },
    ])

    const router = useRouter();
    const store = useStore();

    //     
    const groups = ref([]);
    const displayedGroups = ref([]);
    const page = ref(1);
    const pageSize = 12;
    const loading = ref(false);
    const isEnd = ref(false);

    const sports = ref([]);   //    
    const searchTerm = ref('');   //   
    const userId = computed(() => store.getters['isLogged/userId']);

    const getSportImage = (sport) => {
      const images = {
         : require('@/assets/images/yoga-image.png'),
         : require('@/assets/images/running-image.png'),
         : require('@/assets/images/gym-image.png'),
      };
      return images[sport] || require('@/assets/images/gym-image.png');
    }

    // const { currentPage, totalPages, visibleDatas, fetchdatas, onPageChange } = usePagination(groups, PerPage);

    const carouselGroups = async (id) => {
      try {
        const response = await axios.get('/api/carousel-groups', {
          params: {
            imageId: id,
          }
        });

        const nonLoggedGroups = response.data;
        const isLoggedIn = computed(() => store.getters['isLogged/loggedIn']);
        if (!isLoggedIn.value) {
          groups.value = nonLoggedGroups.map(group => ({
            ...group,
            saved: 0
          }));
        } else {
          groups.value = nonLoggedGroups;
        }
      } catch (error) {
        console.log("Error", error);
      }
    }

    //      
    const groupList = async () => {
      loading.value = true;
      try {
        const response = await axios.get('/api/group-list');

        const nonLoggedGroups = response.data;

        //          false 
        const isLoggedIn = computed(() => store.getters['isLogged/loggedIn']);
        if (!isLoggedIn.value) {
          groups.value = nonLoggedGroups.map(group => ({
            ...group,
            saved: 0
          }));
        } else {
          //   
          groups.value = nonLoggedGroups;
        }
        //    groups 
        // groups.value = response.data;

        // page.value = 1;
        // updateDisplayedGroups();
        //    
      } catch (error) {
        console.error("Error", error);
      } finally {
        loading.value = false;
      }
    };

    //     
    const updateDisplayedGroups = () => {
      const start = (page.value - 1) * pageSize;
      const end = start + pageSize;
      displayedGroups.value = groups.value.slice(start, end);

      //     isEnd true 
      if (displayedGroups.value.length >= groups.value.length) {
        isEnd.value = true;
      }
    };

    const loadMore = () => {
      if (!loading.value && !isEnd.value) {
        loading.value = true;
        page.value++;
        updateDisplayedGroups();
        loading.value = false;
      }
    };

    onBeforeMount(async () => {
      await sportList();
      await groupList();
    });

    // onBeforeMount(() => {
    //   window.removeEventListener('scroll', handleScroll);
    // });

    // onMounted(() => {
    //   window.addEventListener('scroll', handleScroll);
    // });

    // const load = async ($state) => {
    //   if (loading.value) return;
    //   loading.value = true;
    //   try {
    //     const response = await axios.get('/api/group-list', {
    //       params: { page: page.value, perPage: perPage }
    //     });
    //     const json = response.data;

    //     if (json.length < 1) {
    //       $state.complete();    //     
    //     } else {
    //       groups.value.push(...json);
    //       $state.loaded();      //    
    //     }
    //     page.value++;
    //   } catch (error) {
    //     console.error('Error', error);
    //     $state.complete();        //       
    //   } finally {
    //     loading.value = false;
    //   }
    // };

    //    
    const sportList = async () => {
      try {
        const response = await axios.get("/api/filter/exercises");
        const data = response.data;

        sports.value = data.map(item => ({
          key: item.key,
          sport: item.sport
        }));

      } catch (error) {
        console.error("  ", error);
      }
    };

    //        
    const SportGroupList = async (event) => {
      const selectedSportValue = event.target.value;
      console.log(`sport${selectedSportValue}`);

      if (selectedSportValue) {
        try {
          const response = await axios.get("/api/filter/exercises/sport", {
            params: {
              sport: selectedSportValue,
            }
          });
          groups.value = response.data;
        } catch (error) {
          console.error("     .", error);
        }
      }
    };

    //    api
    const locationInput = ref("");

    const openDaumApi = () => {
      new window.daum.Postcode({
        oncomplete: (data) => {
          console.log("  : ", data)
          locationInput.value = data.sigungu;

          fetchLocationData();
        }
      }).open();
    };

    //      
    const fetchLocationData = async () => {
      try {
        console.log(locationInput.value);
        const response = await axios.get("/api/filter/location", {
          params: {
            location: locationInput.value,
          }
        });
        groups.value = response.data;
      } catch (error) {
        console.error(" ", error)
      }
    };

    //   
    const date = ref(null); //   

    const dateClicked = async (date) => {
      try {
        // console.log(`Selected ${date}`);
        const isoDate = date.toISOString();

        const response = await axios.get("/api/filter/date", {
          params: {
            date: isoDate,
          }
        });
        groups.value = response.data;
      } catch (error) {
        console.error('     ', error);
        alert("   ");
      }
    };

    //  
    const timeString = ref('');
    const time = ref({
      hours: null,
      minutes: null,
    });

    const timeSelected = async () => {
      if (timeString.value) {
        try {
          const response = await axios.get("/api/filter/time", {
            params: {
              time: timeString.value,
            }
          });
          groups.value = response.data;
        } catch (error) {
          console.error("API  : ", error);
        }

      } else {
        alert("   .");
      }
    }

    //  
    const searchGroups = async () => {
      if (!searchTerm.value) {
        return;
      }
      try {
        const response = await axios.get("/api/search", {
          params: {
            searchTerm: searchTerm.value,
          }
        });
        groups.value = response.data;
      } catch (error) {
        console.error(" ", error);
      }
    };

    // 
    const selectedGroupList = async () => {
      try {
        const response = await axios.get("/api/sort", {
          params: {
            value: selectedOption.value,
          }
        });
        //    
        groups.value = response.data;
      } catch (error) {
        console.error(" ", error);
      }
    };

    const isTooltipVisible = ref(true);

    const toggleTooltip = () => {
      isTooltipVisible.value = !isTooltipVisible.value;
    };

    //         
    //  (  )    query 
    const showConfirmPopup = ref(false);
    const currentGroupId = ref(null);

    const confirmDeletion = async (communityId) => {
      const group = groups.value.find(group => group.communityId === communityId);

      if (!group) {
        console.error("   .", communityId);
        return;
      }

      try {
        await apiClient.post('/join-group', {
          communityId: communityId,
        });
        router.push({ name: "JoinedGroups" });
        alert("  ")
      } catch (error) {
        if (error.response) {
          if (error.response.status === 400) {
            alert(error.response.data); // 400  
          } else if (error.response.status === 401) {
            alert("   ."); // catch  401  
          } else {
            console.error("Error", error);
          }
        } else {
          console.error("Error", error);
        }
      } finally {
        showConfirmPopup.value = false;
        currentGroupId.value = null;
      }
    };
    //    
    const cancelDeletion = () => {
      showConfirmPopup.value = false;
      currentGroupId.value = null;
    };

    // 
    const likeGroup = async (communityId) => {
      const group = groups.value.find(g => g.communityId === communityId);
      if (!group) return;   //    

      try {
        const method = group.saved ? 'DELETE' : 'POST';
        const url = '/home/like-group';

        const response = await apiClient({
          method: method,
          url: url,
          data: {
            communityId: group.communityId,
            saved: !group.saved,
          },
        });

        if (response.status === 200) {
          group.saved = !group.saved;

        } else {
          console.error(" ", response.data.message);
        }
      } catch (error) {
        if (error.response) {
          if (error.response.status === 400) {
            alert(error.response.data); // 400  
          } else if (error.response.status === 401) {
            alert("   ."); // catch  401  
          } else {
            console.error("Error", error);
          }
        } else {
          console.error("Error", error);
        }
      }
    }

    return {
      getSportImage,
      carouselGroups,
      loadMore,
      fetchLocationData,
      openDaumApi,
      locationInput,
      likeGroup,
      selectedOption,
      options,
      date,
      dateClicked,

      time,
      timeString,
      timeSelected,

      groups,
      displayedGroups,
      loading,
      // load,
      userId,
      page,
      pageSize,

      showConfirmPopup,
      toggleTooltip,
      confirmDeletion,
      cancelDeletion,
      searchTerm,

      searchGroups,
      selectedGroupList,
      sports,

      SportGroupList,
      sportList,
    };
  },
};
</script>

<style scoped>
main {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-rows: 1fr 2fr;
}

/*  */
.carousel {
  position: relative;
  width: 90%;
  max-width: 1320px;
  /* height: auto; */
  /* max-height: 700px; */
  overflow: hidden;
  margin: 0 auto;
}

.carousel-box {
  display: flex;
  transition: transform 0.5s ease-in-out;
}

.carousel-item {
  min-width: 100%;
  /* height: auto; */
  /* carousel   */
  display: flex;
  align-items: center;
  justify-content: center;
  /* font-size: 24px; */
  /* background-color: #ddd; */
  /*    */
}

.slide-images {
  width: 100%;
  height: auto;
  object-fit: contain;
  /*       */
}

.arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 20px;
  cursor: pointer;
  user-select: none;
  z-index: 1;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s, transform 0.3s;
}

.arrow:hover {
  transform: translateY(-50%) scale(1.1);
}

#left {
  left: 10px;
}

#right {
  right: 10px;
}

/*  */
.main-bottom {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-grow: 1;
}

.scroll-container {
  overflow-y: auto;
  max-height: 80vh;
}


.filter {
  width: 1320px;
  height: 900px;
  display: grid;
  grid-template-rows: 100px 40px 4fr;
}

.filter-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
}


.list-select {
  width: 200px;
  height: 48px;
  border-radius: 3px;
  text-align: center;
  color: black;
  background-color: #fff;
  /* border: 0.1px solid grey; */
  font-size: 14px;
  cursor: pointer;
  padding-right: 10px;
  appearance: none;

}

.input-event {
  width: 204px;
  height: 50px;
  border-radius: 3px;
  text-align: center;
  border: 0.1px solid grey;
}

.input-event::placeholder {
  text-align: center;
  font-size: 14px;
}

.date-picker {
  width: 200px;
  height: 48px;
  text-align: center;
  background-color: #fff;
  font-size: 16px;
  cursor: pointer;
}

::v-deep .dp__input::placeholder {
  font-size: 14px;
  color: black;
}

::v-deep .dp__input {
  width: 200px;
  height: 48px;
  border-radius: 3px;
  text-align: center;
  background-color: #fff;
  border: 0.1px solid grey;
  font-size: 16px;
  cursor: pointer;
}

.time-picker {
  width: 200px;
  height: 48px;
  text-align: center;
  background-color: #fff;
  border: 0.1px solid grey;
  border-radius: 3px;
  font-size: 16px;
  cursor: pointer;
}

/* ::v-deep .vue__time-picker input.vue__time-picker-input {
  width: 200px;
  height: 48px;
  border-radius: 3px;
  text-align: center;
  background-color: #fff;
  border: 0.1px solid grey;
  font-size: 16px;
  cursor: pointer;
  color: black;
}

::v-deep .vue__time-picker input.vue__time-picker-input::placeholder {
  font-size: 18px;
  color: black;
} */

.input-slot-image {
  height: 20px;
  width: auto;
  margin-left: 11px;
  margin-top: 5px;
}

/*  */
.search-box {
  display: flex;
  align-items: center;
  border: 1px solid grey;
  border-radius: 3px;
  width: 250px;
  height: 50px;
}

.search-input {
  border: none;
  outline: none;
  padding: 5px 0px 5px 10px;
  font-size: 14px;
}

.search-icon {
  width: 15px;
  height: 15px;
  margin-left: 220px;
  cursor: pointer;
  position: absolute;
}

/*   */
.sort {
  display: flex;
  justify-content: flex-end;
}

.sort-title {
  width: 110px;
  height: 40px;
  text-align: center;
  border-radius: 3px;
  /* color: grey; */
  border: 1px solid grey;
  box-sizing: border-box;
  font-size: 15px;
  cursor: pointer;
  padding-right: 10px;
  /*      */
  -webkit-appearance: none;
  /*    */
  -moz-appearance: none;
  appearance: none;
  background-image: url('@/assets/images/custom-arrow.png');
  /*     */
  background-repeat: no-repeat;
  background-position: right 15px center;
  /*    */
  background-size: 12px;
  /*    */
}

.sort-title option {
  background-color: #ffffff;
  /*   */
  color: #333;
  /*    */
  padding: 10px;
  /*    */
}

/*    */
.group {
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  margin-top: 25px;
}

/*    justify-content   margin  
3 item margin   */
/*   */
.group-container {
  display: grid;
  grid-template-rows: 40px 250px 1fr;
  width: 280px;
  height: 450px;
  margin-right: 66px;
}

.group>.group-container:nth-of-type(4n) {
  margin-right: 0px;
}

.unicode-box {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin: 5px 3px 0px 4px;
}

/*    */
.group-detail-icon {
  width: 25px;
  height: 25px;
  cursor: pointer;
}

.group-detail {
  font-size: 26px;
  cursor: pointer;
  margin-bottom: 3px;
  margin-right: 3px;
  color: grey;
  font-weight: 100;
  margin-top: 2px;
}

.sport-image-box {
  width: 100%;
  height: 250px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.sport-image {
  width: 270px;
  height: 250px;
}

.group-information {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.user-info-and-group-sport {
  width: 100%;
